
@font-face {
  font-family: 'BEBAS NEUE Light';
  src: local('BEBAS NEUE Light'), url('bebas/BebasNeue_Light.ttf') format('truetype');
  font-style: normal;
  font-weight: normal; }

@font-face {
  font-family: 'BEBAS NEUE BOLD';
  src: local('BEBAS NEUE BOLD'), url('bebas/BebasNeue_Bold.ttf') format('truetype');
  font-style: normal;
  font-weight: normal; }

@font-face {
  font-family: 'BEBAS NEUE';
  src: local('BEBAS NEUE'), url('bebas/BebasNeue_Regular.ttf') format('truetype');
  font-style: normal;
  font-weight: normal; }

/* font-family: "RobotoRegular"; */
@font-face {
  font-family: "RobotoRegular";
  src: url("roboto/RobotoRegular.eot");
  src: url("roboto/RobotoRegular.eot?#iefix") format("embedded-opentype"), url("roboto/RobotoRegular.woff") format("woff"), url("roboto/RobotoRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal; }

@font-face {
  font-family: "RobotoLight";
  src: url("roboto/RobotoLight.eot");
  src: url("roboto/RobotoLight.eot?#iefix") format("embedded-opentype"), url("roboto/RobotoLight.woff") format("woff"), url("roboto/RobotoLight.ttf") format("truetype");
  font-style: normal;
  font-weight: normal; }

@font-face {
  font-family: "RobotoMedium";
  src: url("roboto/RobotoMedium.ttf") format("truetype");
  font-style: normal;
  font-weight: normal; }

